import Image from "next/image";
import Link from "next/link";
import AgoTime from "../utils/agoTime";
interface NewsType {
    id: number;
    title: string;
    slug: string;
    thumbnail_url: string;
    category: { name: string };
    published_at: string;
    is_featured: boolean;
    tags: {
        id:number;
        name: string;
    }[];
}

export default function NewsHero({ data }: { data: NewsType[] }) {
    // Filter featured news
    const featuredNews = data?.filter((item) => item.is_featured) || [];

    if (!featuredNews.length) return null;

    const mainStory = featuredNews[0];
    // Change slice to get 4 items (Index 1, 2, 3, 4)
    const subStories = featuredNews.slice(1, 5);

    return (
        <section className="w-full my-8">
            {/* Header Section */}
            <div className="flex justify-between items-center mb-4">
                <div className="bg-[#4c3b71] text-white px-4 py-1.5 rounded-md text-sm font-bold flex items-center gap-2 h-10">
                    <svg xmlns="http://www.w3.org/2000/svg" className="h-4 w-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 20H5a2 2 0 01-2-2V6a2 2 0 012-2h10a2 2 0 012 2v1m2 13a2 2 0 01-2-2V7m2 13a2 2 0 002-2V9a2 2 0 00-2-2h-2m-4-3H9M7 16h6M7 8h6v4H7V8z" />
                    </svg>
                    FUTURE NEWS
                </div>
                <Link href="/news" className="text-[#4c3b71] font-bold text-sm hover:underline flex items-center gap-1">
                    More News
                    <svg xmlns="http://www.w3.org/2000/svg" className="h-4 w-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5l7 7-7 7" />
                    </svg>
                </Link>
            </div>

            <div className="grid grid-cols-1 lg:grid-cols-2 gap-6 h-auto lg:h-[600px]">
                {/* LEFT: MAIN STORY (Big Card) */}
                <Link
                    href={`/news/${mainStory.slug}`}
                    className="relative w-full h-64 lg:h-full rounded-xl overflow-hidden shadow-sm group block"
                >
                    <Image
                        src={`https://pub-64a1f52f8ce34898ad37705d90a1d23b.r2.dev/${mainStory.thumbnail_url}`}
                        alt={mainStory.title}
                        fill
                        className="object-cover transition-transform duration-500 group-hover:scale-105"
                    />
                    <div className="absolute inset-0 bg-gradient-to-t from-black/90 via-black/20 to-transparent"></div>

                    <div className="absolute bottom-0 left-0 p-6 text-white w-full">
            <span className="bg-red-600 text-xs font-bold px-2 py-1 rounded mb-3 inline-block">
              {mainStory.category.name}
            </span>
                        <h2 className="text-2xl md:text-3xl font-bold leading-tight mb-2 group-hover:text-blue-200 transition-colors line-clamp-2">
                            {mainStory.title}
                        </h2>
                        <p className="text-gray-300 text-xs mt-1 opacity-90">
                            {new Date(mainStory.published_at).toLocaleDateString()}
                        </p>
                    </div>
                </Link>

                {/* RIGHT: 4 SMALL STORIES (List Layout) */}
                <div className="flex flex-col gap-3 h-full">
                    {subStories.map((item) => (
                        <Link
                            key={item.id}
                            href={`/news/${item.slug}`}
                            className="flex gap-4 bg-white p-3 rounded-xl border border-gray-100/50 shadow-sm hover:shadow-md hover:border-blue-100 hover:-translate-y-1 transition-all duration-300 group h-full items-center"
                        >
                            {/* Thumbnail Image with Ring Effect */}
                            <div className="relative w-32 h-24 flex-shrink-0 rounded-lg overflow-hidden border border-gray-100 group-hover:border-blue-200 transition">
                                <Image
                                    src={`https://pub-64a1f52f8ce34898ad37705d90a1d23b.r2.dev/${item.thumbnail_url}`}
                                    alt={item.title}
                                    fill
                                    className="object-cover group-hover:scale-110 transition-transform duration-700"
                                />
                                {/* Play Icon Overlay if it's a video (Optional design touch) */}
                                <div className="absolute inset-0 bg-black/10 group-hover:bg-transparent transition-colors" />
                            </div>

                            {/* Text Content */}
                            <div className="flex flex-col flex-grow py-2 justify-between">
                                {/* Top Row: Category + Date */}
                                <div className="flex items-center justify-between mb-1">
                                    <span className="text-[10px] font-black tracking-wider text-yellow-600 uppercase bg-gray-100 px-2 py-0.5 rounded-full">
                                      {item.category.name}
                                    </span>
                                </div>

                                {/* Title */}
                                <h3 className="text-sm font-bold text-gray-800 leading-snug line-clamp-2 group-hover:text-blue-700 transition-colors mb-1">
                                    {item.title}
                                </h3>
                                <div className="flex items-center gap-1 text-[10px] text-[#4c3b71] font-medium">
                                    {agoTime(item.published_at)}
                                </div>
                                {/* Tags */}
                                <div className="flex flex-wrap gap-2 mt-1">
                                    {item.tags?.map((tags) => (
                                        <span
                                            key={tags.id}
                                            className="text-[10px] font-medium text-gray-700 bg-gray-100 px-2 py-0.5 rounded-full hover:bg-blue-100 transition"
                                        >
                                          #{tags.name}
                                        </span>
                                    ))}
                                </div>
                            </div>

                        </Link>
                    ))}
                </div>
            </div>
        </section>
    );
}